import React, { useEffect, useState } from "react";
import { HiBookmark, HiOutlineBookmark, HiOutlineX } from "react-icons/hi";

import './cardpage.css';
import LoadingAnim from "../../Components/LoadingAnim/LoadingAnim";
import { Link } from "react-router-dom";

interface Card {
    title: string,
    favorited: boolean,
    author: string,
    partA: string,
    partB: string,
    partC: string,
    _id: string
}

const CardPage = () => {
    const [card, setCard] = useState<Card|null>(null);
    const [favoriteState, setFavoriteState] = useState(false);

    function getURL(rawUrl: string): string {
        return rawUrl.slice(rawUrl.indexOf("?"))
    }

    function capitalizeTitle(string: string): string {
        const wordArray = string.split(" ");
        return wordArray.map(word => word.charAt(0).toUpperCase() + word.slice(1)).join(" ");
    }

    function handleFavoriteAction(event: React.MouseEvent) {
        event.stopPropagation();
        setFavoriteState(!favoriteState);
    }
    
    useEffect(() => {
        const id = new URLSearchParams(getURL(window.location.href)).get("id");
        fetch(`https://container-service-1.01c6s0c46j1b0.us-east-1.cs.amazonlightsail.com/reads/byId?id=${id}`)
            .then(blob => blob.json())
            .then(data => setCard(data));
    }, []);

    return (
        <>{
            card
            ? <div className="fullscreen-card">
                <h1>{capitalizeTitle(card.title)}</h1>
                <p>A. {card.partA}</p>
                <p>B. {card.partB}</p>
                <p>C. {card.partC}</p>
                <div className="bottom-bar">
                    <p>By {card.author}</p>
                    <div>
                        { favoriteState ? <HiBookmark onClick={handleFavoriteAction} size="20px"/> : <HiOutlineBookmark onClick={handleFavoriteAction} size="20px"/> }
                        <Link to="/">
                            <HiOutlineX size="20px" color="black"/>
                        </Link>
                    </div>
                </div>
            </div>
            : <LoadingAnim/>
        }</>
    );
};

export default CardPage;
