import React from "react";

interface Props {
    partA: string
    partB: string
    partC: string
    concept: string
    author: string
    _id: string
}

const AdminCard = (props: Props) => {
    function deleteCard() {
        fetch("https://container-service-1.01c6s0c46j1b0.us-east-1.cs.amazonlightsail.com/writes/deleteCard", {
            method: "DELETE",
            headers: {
                "Access-Control-Allow-Origin": "*",
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ id: props._id }) 
        })
            .then(() => window.location.reload())
    }

    return (
        <div className="admin-card">
            <b>{props.concept}</b>
            <p>A. {props.partA}</p>
            <p>B. {props.partB}</p>
            <p>C. {props.partC}</p>
            <em>Author: {props.author}</em>
            <br/>
            <button className="delete-button" onClick={deleteCard}>Delete</button>
        </div>
    );
};

export default AdminCard;
