import React, { useEffect, useState } from 'react';

import './App.css';
import Topbar from './Components/Topbar/Topbar';
import Sidebar from './Components/Sidebar/sidebar';
import Card from './Components/Card/card';
import LoadingAnim from './Components/LoadingAnim/LoadingAnim';

interface ApiResult {
    title: string,
    author: string,
    _id: string,
    chapter: number,
    partA: string,
    partB: string,
    partC: string
}

function App() {
    const [apiData, setApiData] = useState<ApiResult|null>(null);

    useEffect(() => {
        fetch("https://container-service-1.01c6s0c46j1b0.us-east-1.cs.amazonlightsail.com/reads/random")
            .then(blob => blob.json())
            .then(data => setApiData(data));
    }, []);

    if (!localStorage.getItem("hasSeenAlert")) {
        alert("The CCCP exists as an online resource for concept card help. Coping cards verbatim, in addition to being morally wrong, violates MCPS policies on academic integrity. All graded work must be original.");
        localStorage.setItem("hasSeenAlert", "true");
    }

    return <>
        <Topbar title="The CCCP"/>
        <div className="featured-card-wrapper" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
            {
                apiData
                ? <Card
                    concept={apiData.title}
                    author={apiData.author}
                    favorited={false}
                    partA={apiData.partA}
                    partB={apiData.partB}
                    partC={apiData.partC}
                    _id={apiData._id}
                />
                : <LoadingAnim/>
                
            }
        </div>
        <Sidebar/>
    </>;
}

export default App;
