import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

import Sidebar from "../../Components/Sidebar/sidebar";
import Topbar from "../../Components/Topbar/Topbar";
import './addpage.css';

const AddPage = () => {
    const [title, setTitle] = useState("");
    const [author, setAuthor] = useState("");
    const [chapter, setChapter] = useState(0);
    const [partA, setPartA] = useState("");
    const [partB, setPartB] = useState("");
    const [partC, setPartC] = useState("");

    const navigate = useNavigate();

    function validateUpload() {
        if (title && author && chapter && partA && partB && partC) {
            return true;
        }

        return false;
    }

    function uploadCard() {
        if (validateUpload()) {
            fetch("https://container-service-1.01c6s0c46j1b0.us-east-1.cs.amazonlightsail.com/writes/addCard", {
                method: "POST",
                headers: {
                    "Access-Control-Allow-Origin": "*",
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    title: title.toLowerCase(),
                    author: author,
                    chapter: chapter,
                    partA: partA,
                    partB: partB,
                    partC: partC
                })
            })
                .then(blob => blob.json())
                .then(data => navigate("/"));
        } else {
            alert("At least one field is blank");
        }
    }

    return (
        <>
            <Topbar title="Add Card"/>
            <div className="textbox-wrapper">
                <input value={title} onChange={(event) => setTitle(event.target.value)} placeholder="Concept Title"/>
                <input value={chapter} onChange={(event) => setChapter(+event.target.value)} placeholder="Chapter" type="number"/>
                <input value={author} onChange={(event) => setAuthor(event.target.value)} placeholder="Author"/>
                <textarea value={partA} onChange={(event) => setPartA(event.target.value)} placeholder="A. Definition of the term"></textarea>
                <textarea value={partB} onChange={(event) => setPartB(event.target.value)} placeholder="B. An example or an application of the concept. You can also include counter-examples if it seems appropriate. Think about what type of example you might be able to use on a free response question. Your example should be from within the last 15 years."></textarea>
                <textarea value={partC} onChange={(event) => setPartC(event.target.value)} placeholder="C. Why is this important to the big picture?  What else does it relate to? Describe the impact of the concept on another political institution, political actor, and/or political behavior."></textarea>

                <button onClick={uploadCard}>Upload</button>
            </div>
            <Sidebar/>
        </>
    );
};

export default AddPage;
