import React, { useState } from "react";
import { HiOutlineSearch } from "react-icons/hi";

import './searchpage.css';
import Sidebar from "../../Components/Sidebar/sidebar";
import Topbar from "../../Components/Topbar/Topbar";
import LoadingAnim from "../../Components/LoadingAnim/LoadingAnim";
import SearchResults from "./SearchResults/SearchResults";

const SearchPage = () => {
    const [searchResults, setSearchResults] = useState([]);
    const [isActiveQuery, setIsActiveQuery] = useState(false);
    const [searchBox, setSearchBox] = useState("");

    function handleSearchBoxEnter (event: React.KeyboardEvent) {
        if (event.key === "Enter") {
            event.stopPropagation();
            setIsActiveQuery(true);

            fetch(`https://container-service-1.01c6s0c46j1b0.us-east-1.cs.amazonlightsail.com/reads/search?search=${searchBox}`)
                .then(blob => blob.json())
                .then(data => {
                    setSearchResults(data);
                    setIsActiveQuery(false);
                });
        }
    }

    return (
        <>
            <Topbar title="Search"/>
            <div className="searchpage-content-wrapper">
                <div className="searchbar">
                    <HiOutlineSearch size="16px"/>
                    <input value={searchBox} onKeyDown={(event) => handleSearchBoxEnter(event)} onChange={(event) => setSearchBox(event.target.value)}/>
                </div>
                <div className="search-results">{ isActiveQuery ? <LoadingAnim/> : <SearchResults results={searchResults}/> }</div>
            </div>
            <Sidebar/>
        </>
    );
};

export default SearchPage;
