import React, { useState } from "react";
import { HiOutlineBookmark, HiBookmark } from "react-icons/hi";
import { MdFullscreen } from "react-icons/md";

import './card.css';

interface Props {
    concept: string,
    favorited: boolean,
    author: string,
    partA: string,
    partB: string,
    partC: string,
    _id: string
}

const Card = (props: Props) => {
    const [sideState, setSideState] = useState(false);
    const [favoriteState, setFavoriteState] = useState(props.favorited);

    function handleFavoriteAction(event: React.MouseEvent) {
        event.stopPropagation();
        setFavoriteState(!favoriteState);
    }

    function truncate(string: string, length: number): string {
        let finalString = "";
        const letterArray = string.split("");

        for (let i = 0; i < length; i++) {
            finalString += letterArray[i];
        }

        return finalString;
    }

    function capitalizeTitle(string: string): string {
        const wordArray = string.split(" ");
        return wordArray.map(word => word.charAt(0).toUpperCase() + word.slice(1)).join(" ");
    }

    function handleFullscreen(event: React.MouseEvent) {
        event.stopPropagation();
        window.location.href = `#/card?id=${props._id}`;
    }

    return (
        <div className={"card "  + (sideState ? "flipped" : "")} onClick={() => setSideState(!sideState)}>
            <div className="content">
                <div className="front">
                    <div className="card-title-wrapper">
                        <b>{capitalizeTitle(props.concept)}</b>
                    </div>
                    <div className="card-bottom-info">
                        <p>{props.author}</p>
                        {
                            favoriteState 
                            ? <HiBookmark onClick={(event) => handleFavoriteAction(event)}/> 
                            : <HiOutlineBookmark onClick={(event) => handleFavoriteAction(event)}/>
                        }
                    </div>
                </div>
                <div className="back">
                    <MdFullscreen className="fullscreen-icon" size="24px" onClick={handleFullscreen}/>
                    <p>A. {truncate(props.partA, 40)}...</p>
                    <p>B. {truncate(props.partB, 40)}...</p>
                    <p>C. {truncate(props.partC, 40)}...</p>
                </div>
            </div>
        </div>
    );
};

export default Card;