import React, { useState } from "react";
import ReactDOM from "react-dom";
import { useNavigate } from "react-router-dom";

import './adminpage.css';
import Topbar from "../../Components/Topbar/Topbar";
import Sidebar from "../../Components/Sidebar/sidebar";
import AdminContent from "./AdminContent/AdminContent";

const AdminPage = () => {
    const [adminLogin, setAdminLogin] = useState("");
    const navigate = useNavigate();

    // Acceses the admin api to authenticate users
    function login() {
        fetch("https://container-service-1.01c6s0c46j1b0.us-east-1.cs.amazonlightsail.com/admin/auth", {
            method: "POST",
            headers: {
                "Access-Control-Allow-Origin": "*",
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ login: adminLogin })
        })
        .then(blob => {
            if (blob.status === 204) {
                // All good
                ReactDOM.render(<AdminContent/>, document.querySelector('.admin-login-view'));
            } else {
                // Failed login
                // Send back to main page
                navigate("/");
            }
        });
    }

    return (
        <>
            <Topbar title="Admin"/>
            <div id="admin-page-content">
                <div className="admin-login-view">
                    <p>Login to Admin Console</p>
                    <input value={adminLogin} onChange={(event) => setAdminLogin(event.target.value)} type="password"/>
                    <button onClick={login}>Login</button>
                </div>
            </div>
            <Sidebar/>
        </>
    );
};

export default AdminPage;
