import React, { useEffect, useState } from "react";

import './admincontent.css';
import AdminCard from "./AdminCard";

interface IAdminCard {
    _id: string
    title: string
    author: string
    partA: string
    partB: string
    partC: string
};

const AdminContent = () => {
    const [skipState, setSkipState] = useState(0);
    const [cards, setCards] = useState<IAdminCard[]>([]);

    useEffect(() => {
        fetch(`https://container-service-1.01c6s0c46j1b0.us-east-1.cs.amazonlightsail.com/admin/loadCards?skip=${skipState}`)
            .then(blob => blob.json())
            .then(data => setCards(cards.concat(data)));

        setSkipState(skipState + 5);
    }, []);

    function loadMore() {
        fetch(`https://container-service-1.01c6s0c46j1b0.us-east-1.cs.amazonlightsail.com/admin/loadCards?skip=${skipState}`)
            .then(blob => blob.json())
            .then(data => setCards(cards.concat(data)));

        setSkipState(skipState + 5);
    }
    
    return (
        <div className="admin-cards-wrapper">
            { cards.map(card => <AdminCard concept={card.title} author={card.author} partA={card.partA} partB={card.partB} partC={card.partC} _id={card._id} key={card._id}/> ) }
            <button onClick={loadMore}>Load More</button>
        </div>
    );
};

export default AdminContent;
